import './css/styles.css';
import pictureCardTpl from './templates/gallery-card.hbs';
import NewsApiService from './js/pictures-service';
import Notiflix from 'notiflix';

const refs = {
  searchForm: document.querySelector('#search-form'),
  loadMoreBtn: document.querySelector('.load-more'),
  picturesContainer: document.querySelector('.gallery'),
};

const newsApiService = new NewsApiService();

refs.searchForm.addEventListener('submit', onSearch);
refs.loadMoreBtn.addEventListener('click', onLoadMore);

function onLoadMore() {
  newsApiService.fetchPictures().then(appendPicturesMarcup); //     /  
}

function onSearch(e) {
  e.preventDefault();

  const form = e.currentTarget;
  newsApiService.query = form.elements.searchQuery.value; //  input  ,   set

  if (newsApiService.query === '') {
    return Notiflix.Notify.success(' - ');
  }

  newsApiService.resetPage();
  newsApiService
    .fetchPictures()
    .then(pictures => {
      clearPicturesContainer();
      appendPicturesMarcup(pictures);
      showLoadMoreBtn();
    }) //     /    /  
    .catch(onFetchError)
    .finally(() => form.reset());
}

// processing resolve /   
function appendPicturesMarcup(pictures) {
  refs.picturesContainer.insertAdjacentHTML('beforeend', pictureCardTpl(pictures)); //     
}

//   
function showLoadMoreBtn() {
  refs.loadMoreBtn.classList.remove('is-hidden');
}

//     
function clearPicturesContainer() {
  refs.picturesContainer.innerHTML = '';
}

// processing reject /   
function onFetchError(error) {
  Notiflix.Notify.failure('! 2', error);
}
